@import "../../../shared/vars";
@import "../../../shared/main_mixins";


.error_404 {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.error_img {
  width: 50%;
  margin: 50px 0;
}

.error_text {
  padding-top: 10px;
  position: relative;
  font-weight: bold;
  margin-bottom: 70px;
  color: rgba(0, 0, 0, 0.5);
  text-align: center;
}

@media (max-width: 767px) {
  .error_img {
    width: 100%;
  }

}

.btns {
  display: flex;
  margin-bottom: 140px;
  justify-content: space-between;
}

.btn1 {
  background-color: #FFFFFF;
  border: 2px solid $main_color;
  box-sizing: border-box;
  border-radius: 20px;
  transition: color $animation_duration, background-color $animation_duration, box-shadow $animation_duration;
}

.btn1:hover {
  p {
    color: white;
  }

  box-shadow: 3px 3px 30px rgba(78, 93, 228, 0.8);
  background-color: $main_color;
  color: white;
}

.btn1 a {
  @include clear_link($main_color);
}

.btn1 p {
  padding: 5px 25px;
  width: 100%;
  height: 100%;
  font-weight: bold;
  font-size: 20px;


  @include center_block();
}

